import { Link } from "react-router-dom";
import styled from 'styled-components';


const SLink = styled(Link)`
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
`;

function MyPage(){
    return (
        <>
        MyPage
        <SLink to="mypage/email"> </SLink>
        <SLink to="mypage/password"> </SLink>
        <SLink to="mypage/withdrawal">  </SLink>
        <SLink to="mypage/myarticle">    </SLink>
        <SLink to="mypage/myrecommend">    </SLink>
        </>
    );
}

export default MyPage;